.box {
    margin: 4px;
    width: 160px;
    /* height: 360px; */
    display: flex;
    flex-direction: column;
}
.box:hover .front{
    display: none;
}

.box:hover .back{
    display: block;
}

.front {
    padding: 0;
    height: 240px;
}

.back {
    display: none;
    padding: 12px;
    height: 216px;
    background-color: black;
    color: white;
}

.title {
    margin-bottom: 12px;
    font-size: 14px;
    font-weight: 700;
}

.subTitle {
    font-size: 12px;
    margin: 8px;
}

.img {
    width: 160px;
}

.subText {
    word-wrap: break-word;
    margin: 0;
    font-size: 13px;
}
.description {
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 3;
    font-size: 11px;
    color: #b0b0b0;
}

a {
    text-decoration: none;
    color: initial;
}