.card {
    padding: 30px;
    display: flex;
    justify-content: space-between;
}

.left {
    display: flex;
    flex-direction: column;
    width: 60%;
}

.right {
}

.title {
    font-size: 36px;
    font-weight: 600;
    margin-bottom: 0;
}

.vertilcalList {
    list-style-type: none;
    padding: 0;
    margin: 0 4px;
}

.listItem {
    float: left;
    border: solid 1px grey;
    border-radius: 4px;
    padding: 4px;
    margin: 2px;

    color: grey;
}

.listText {
    float: left;
    margin: 4px;
    font-size: 14px;
    font-weight: 600;
}